
.main{
  background: url(../../../assets/images/i_bg.jpg) no-repeat;
  background-size: 100% 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;

  .title{
    height: 50px;
    color: #fff;
    font-size: 25px;
    letter-spacing: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
  }

  .con{
    flex: 1;
    overflow-y: scroll;
  }

  .modelist{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 20px;
    
  }
}

